import React, { useEffect, useState } from 'react';
import { DisposableDelegate } from '@lumino/disposable';
import { ReactWidget, ToolbarButton } from '@jupyterlab/apputils';
import { LabIcon } from '@jupyterlab/ui-components';
import { Widget } from '@lumino/widgets';
import { URLExt } from '@jupyterlab/coreutils';
import { ServerConnection } from '@jupyterlab/services';
import loading from '../style/ppt_view/loading.gif';
import pptViewStr from '../style/ppt_view/ppt-view.svg';
const subWindowRef = React.createRef();
let observer;
/**
 * PPTComponent
 * @param props
 * @returns
 */
const PPTComponent = (props) => {
    const [showLoading, setShowLoading] = useState(true);
    let needDisplay;
    let canBeDistroy;
    useEffect(() => {
        console.log(`path: ${props.path}`);
        console.log('create PPTComponent...');
        needDisplay = true;
        canBeDistroy = false;
        return function cleanup() {
            console.log('destroy PPTComponent...');
        };
    });
    const startRise = () => {
        const subDocument = window.notebook.document;
        const config = { attributes: false, childList: true, subtree: true };
        const callback = (mutationRecords, observer) => {
            for (let mutationRecord of mutationRecords) {
                if (mutationRecord.type === 'childList') {
                    // const node: Node = mutationRecord.target;
                    if (needDisplay) {
                        const riseBtn = window.notebook.document.getElementById('RISE');
                        if (riseBtn) {
                            console.log('start RISE...');
                            setShowLoading(false);
                            riseBtn.click();
                            subWindowRef.current.style.width = '100%';
                            subWindowRef.current.style.height = '100%';
                            needDisplay = false;
                            canBeDistroy = true;
                            break;
                        }
                    }
                    else if (canBeDistroy) {
                        const exitBtn = window.notebook.document.getElementById('exit_b');
                        if (!exitBtn) {
                            console.log('exit RISE...');
                            observer.disconnect();
                            canBeDistroy = false;
                            subWindowRef.current.style.width = '0';
                            subWindowRef.current.style.height = '0';
                            props.close();
                        }
                    }
                }
            }
        };
        observer = new MutationObserver(callback);
        observer.observe(subDocument.body, config);
    };
    return (React.createElement("div", { style: { "height": "100vh", "width": "100vw", "background": "rgba(0,0,0,0.45)" } },
        React.createElement("div", { ref: subWindowRef, style: { "height": "0", "width": "0" } },
            React.createElement("iframe", { name: "notebook", src: props.path, 
                // src="http://simeondemacbook-pro.local:8888/notebooks/files/matplotlib.tmp-ipynb.ipynb"
                height: "100%", width: "100%", scrolling: "auto", frameBorder: "0", onLoad: () => startRise() })),
        React.createElement("img", { src: loading, style: {
                display: showLoading ? "block" : "none",
                "height": "80px", "width": "80px", "position": "fixed", "top": "48%", "left": "48%"
            } })));
};
/**
 * PPTTabWidget
 */
class PPTTabWidget extends ReactWidget {
    constructor(context) {
        super();
        this.addClass('ppt-widgets-view');
        this.id = 'ppt-widget';
        this.context = context;
    }
    gen_notebook_url() {
        const settings = ServerConnection.makeSettings();
        const url = URLExt.join(settings.baseUrl, 'notebooks', this.context.path);
        return url;
    }
    close() {
        console.log('revert');
        this.context.revert();
        super.close();
    }
    render() {
        return (React.createElement(PPTComponent, { path: this.gen_notebook_url(), close: () => this.close() }));
    }
}
/**
 * PPT view button
 */
class PPTViewButton {
    createNew(panel, context) {
        const icon = new LabIcon({
            name: 'launcher:ppt-view-icon',
            svgstr: pptViewStr,
        });
        const openPPTView = () => {
            const pptWidget = new PPTTabWidget(context);
            Widget.attach(pptWidget, document.body);
        };
        const button = new ToolbarButton({
            className: 'ppt-view-button',
            icon: icon,
            tooltip: 'PPT',
            onClick: openPPTView,
        });
        panel.toolbar.insertItem(10, 'openPPTViews', button);
        return new DisposableDelegate(() => {
            button.dispose();
        });
    }
}
/**
 * Initialization data for the @pi-lab-extension/ppt-view extension.
 */
export const pptViewPlugin = {
    id: 'pi_lab_jupyter_extension:ppt_view_plugin',
    autoStart: true,
    activate: (app) => {
        console.log('插件 pi_lab_jupyter_extension:ppt_view_plugin 已激活.');
        app.docRegistry.addWidgetExtension('Notebook', new PPTViewButton());
    }
};
